"use client"
import React, { useState, useMemo } from 'react';
import { Send, Sparkles, Code, Clock, Zap, CheckCircle, AlertCircle, Loader2, Brain } from 'lucide-react';

const ProjectAnalyzer = () => {
  const [prompt, setPrompt] = useState('');
  const [response, setResponse] = useState('');
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');
  const [metadata, setMetadata] = useState(null);

  const analyzeProject = async () => {
    if (!prompt.trim()) {
      setError('Please describe your project idea');
      return;
    }

    setLoading(true);
    setError('');
    setResponse('');
    setMetadata(null);

    try {
      const res = await fetch('/api/analyze', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({ prompt: prompt.trim() }),
      });

      const data = await res.json();

      if (!res.ok) {
        throw new Error(data.error || 'Failed to analyze project');
      }

      // Validate response data
      if (!data.result || typeof data.result !== 'string') {
        throw new Error('Invalid response format from server');
      }

      setResponse(data.result);
      setMetadata(data.metadata);
    } catch (err) {
      setError(err.message || 'Something went wrong. Please try again.');
    } finally {
      setLoading(false);
    }
  };

  const handleKeyPress = (e) => {
    if (e.key === 'Enter' && (e.ctrlKey || e.metaKey)) {
      analyzeProject();
    }
  };

  // Enhanced HTML sanitization function
  const sanitizeHtml = (html) => {
    // Create a temporary div to parse HTML
    const temp = document.createElement('div');
    temp.innerHTML = html;

    // Remove potentially dangerous elements and attributes
    const dangerousElements = ['script', 'iframe', 'object', 'embed', 'form', 'input', 'textarea', 'button'];
    dangerousElements.forEach(tag => {
      const elements = temp.querySelectorAll(tag);
      elements.forEach(el => el.remove());
    });

    // Remove dangerous attributes
    const dangerousAttrs = ['onclick', 'onload', 'onerror', 'onmouseover', 'onfocus', 'onblur', 'onchange', 'onsubmit'];
    const allElements = temp.querySelectorAll('*');
    allElements.forEach(el => {
      dangerousAttrs.forEach(attr => {
        if (el.hasAttribute(attr)) {
          el.removeAttribute(attr);
        }
      });

      // Remove javascript: urls
      ['href', 'src', 'action'].forEach(attr => {
        const value = el.getAttribute(attr);
        if (value && value.toLowerCase().startsWith('javascript:')) {
          el.removeAttribute(attr);
        }
      });
    });

    return temp.innerHTML;
  };

  // Improved response formatting with better regex handling
  const formatResponse = (text) => {
    if (!text || typeof text !== 'string') {
      return '<p>Invalid response format</p>';
    }

    try {
      // Escape HTML entities first to prevent XSS
      let formatted = text
        .replace(/&/g, '&amp;')
        .replace(/</g, '&lt;')
        .replace(/>/g, '&gt;')
        .replace(/"/g, '&quot;')
        .replace(/'/g, '&#x27;');

      // Process in specific order to avoid conflicts
      formatted = formatted
        // 1. Code blocks first (most specific, uses ``` markers)
        .replace(/```([\s\S]*?)```/g, '<pre class="bg-gray-100 p-3 rounded overflow-x-auto border"><code>$1</code></pre>')

        // 2. Inline code (single backticks)
        .replace(/`([^`\n]+)`/g, '<code class="bg-gray-100 px-1 py-0.5 rounded text-sm font-mono">$1</code>')

        // 3. Bold text (double asterisks) - more specific first
        .replace(/\*\*([^*\n]+?)\*\*/g, '<strong>$1</strong>')

        // 4. Italic text (single asterisks) - after bold to avoid conflicts
        .replace(/(?<!\*)\*([^*\n]+?)\*(?!\*)/g, '<em>$1</em>')

        // 5. Headers (process from most specific to least specific)
        .replace(/^### ([^\n]+)$/gm, '<h3 class="text-lg font-semibold text-gray-800 mt-4 mb-2">$1</h3>')
        .replace(/^## ([^\n]+)$/gm, '<h2 class="text-xl font-bold text-gray-900 mt-6 mb-3 flex items-center gap-2"><span class="text-blue-600">$1</span></h2>')
        .replace(/^# ([^\n]+)$/gm, '<h1 class="text-2xl font-bold text-gray-900 mt-6 mb-4">$1</h1>')

        // 6. Checkboxes and lists
        .replace(/^- \[ \] ([^\n]+)$/gm, '<div class="flex items-start gap-2 my-1"><input type="checkbox" class="mt-1" disabled><span>$1</span></div>')
        .replace(/^- \[x\] ([^\n]+)$/gm, '<div class="flex items-start gap-2 my-1"><input type="checkbox" class="mt-1" checked disabled><span>$1</span></div>')
        .replace(/^- ([^\n]+)$/gm, '<div class="ml-4 my-1 flex items-start gap-2"><span class="text-blue-600 font-bold">•</span><span>$1</span></div>')

        // 7. Numbered lists
        .replace(/^\d+\. ([^\n]+)$/gm, '<div class="ml-4 my-1 flex items-start gap-2"><span class="text-blue-600 font-bold min-w-[20px]">$&</span></div>')

        // 8. Paragraphs (double line breaks)
        .replace(/\n\n+/g, '</p><p class="mb-3">')

        // 9. Single line breaks
        .replace(/\n/g, '<br>');

      // Wrap in paragraph if not empty
      if (formatted.trim()) {
        formatted = `<div class="space-y-3"><p class="mb-3">${formatted}</p></div>`;
      }

      // Sanitize the HTML before returning
      return sanitizeHtml(formatted);
    } catch (err) {
      console.error('Error formatting response:', err);
      return `<p class="text-red-600">Error formatting response. Raw content:</p><pre class="bg-gray-100 p-3 rounded mt-2 text-sm overflow-x-auto">${text}</pre>`;
    }
  };

  // Memoize formatted response to avoid re-processing on every render
  const formattedResponse = useMemo(() => {
    if (!response) return '';
    return formatResponse(response);
  }, [response]);

  const examplePrompts = [
    "A task management app with real-time collaboration features",
    "An AI-powered recipe recommendation system",
    "A mobile app for tracking fitness goals with social features",
    "A web platform for freelance project management"
  ];

  return (
    <div className="min-h-screen bg-gradient-to-br from-blue-50 via-white to-purple-50">
      <div className="container mx-auto px-4 py-8 max-w-6xl">
        {/* Header */}
        <div className="text-center mb-8">
          <div className="flex items-center justify-center gap-3 mb-4">
            <div className="p-3 bg-gradient-to-r from-blue-600 to-purple-600 rounded-xl">
              <Brain className="w-8 h-8 text-white" />
            </div>
            <h1 className="text-4xl font-bold bg-gradient-to-r from-blue-600 to-purple-600 bg-clip-text text-transparent">
              AI Project Analyzer
            </h1>
          </div>
          <p className="text-gray-600 text-lg max-w-2xl mx-auto">
            Transform your project ideas into detailed development roadmaps with expert guidance, 
            tech stack recommendations, and step-by-step implementation plans.
          </p>
        </div>

        {/* Main Content */}
        <div className="grid grid-cols-1 lg:grid-cols-2 gap-8">
          {/* Input Section */}
          <div className="space-y-6">
            <div className="bg-white rounded-2xl shadow-lg border border-gray-100 p-6">
              <div className="flex items-center gap-3 mb-4">
                <Sparkles className="w-6 h-6 text-blue-600" />
                <h2 className="text-xl font-semibold text-gray-800">Describe Your Project</h2>
              </div>

              <textarea
                value={prompt}
                onChange={(e) => setPrompt(e.target.value)}
                onKeyDown={handleKeyPress}
                placeholder="Describe your project idea in detail. Include features, target audience, and any specific requirements..."
                className="w-full h-40 p-4 border border-gray-200 rounded-xl resize-none focus:ring-2 focus:ring-blue-500 focus:border-transparent outline-none transition-all"
                disabled={loading}
                maxLength={5000}
              />

              <div className="flex items-center justify-between mt-4">
                <span className="text-sm text-gray-500">
                  {prompt.length}/5000 characters • Press Ctrl+Enter to analyze
                </span>
                <button
                  onClick={analyzeProject}
                  disabled={loading || !prompt.trim()}
                  className="flex items-center gap-2 px-6 py-3 bg-gradient-to-r from-blue-600 to-purple-600 text-white rounded-xl hover:from-blue-700 hover:to-purple-700 disabled:opacity-50 disabled:cursor-not-allowed transition-all font-medium"
                >
                  {loading ? (
                    <>
                      <Loader2 className="w-5 h-5 animate-spin" />
                      Analyzing...
                    </>
                  ) : (
                    <>
                      <Send className="w-5 h-5" />
                      Analyze Project
                    </>
                  )}
                </button>
              </div>
            </div>

            {/* Example Prompts */}
            <div className="bg-white rounded-2xl shadow-lg border border-gray-100 p-6">
              <h3 className="text-lg font-semibold text-gray-800 mb-4 flex items-center gap-2">
                <Zap className="w-5 h-5 text-yellow-600" />
                Example Project Ideas
              </h3>
              <div className="space-y-2">
                {examplePrompts.map((example, index) => (
                  <button
                    key={index}
                    onClick={() => setPrompt(example)}
                    className="w-full text-left p-3 text-gray-700 hover:bg-blue-50 rounded-lg transition-colors border border-gray-100 hover:border-blue-200"
                    disabled={loading}
                  >
                    {example}
                  </button>
                ))}
              </div>
            </div>

            {/* Features */}
            <div className="bg-white rounded-2xl shadow-lg border border-gray-100 p-6">
              <h3 className="text-lg font-semibold text-gray-800 mb-4">What You'll Get</h3>
              <div className="space-y-3">
                {[
                  { icon: Code, text: "Detailed tech stack recommendations" },
                  { icon: Clock, text: "Step-by-step development timeline" },
                  { icon: CheckCircle, text: "Best practices and pro tips" },
                  { icon: Sparkles, text: "Learning resources and tutorials" }
                ].map((feature, index) => (
                  <div key={index} className="flex items-center gap-3 text-gray-700">
                    <feature.icon className="w-5 h-5 text-blue-600" />
                    <span>{feature.text}</span>
                  </div>
                ))}
              </div>
            </div>
          </div>

          {/* Output Section */}
          <div className="space-y-6">
            <div className="bg-white rounded-2xl shadow-lg border border-gray-100 p-6 min-h-[600px]">
              <div className="flex items-center gap-3 mb-4">
                <Brain className="w-6 h-6 text-purple-600" />
                <h2 className="text-xl font-semibold text-gray-800">Analysis Results</h2>
              </div>

              {error && (
                <div className="flex items-start gap-3 p-4 bg-red-50 border border-red-200 rounded-xl text-red-700">
                  <AlertCircle className="w-5 h-5 mt-0.5 flex-shrink-0" />
                  <div>
                    <p className="font-medium">Error</p>
                    <p className="text-sm">{error}</p>
                  </div>
                </div>
              )}

              {loading && (
                <div className="flex flex-col items-center justify-center py-12 text-gray-500">
                  <Loader2 className="w-12 h-12 animate-spin mb-4 text-blue-600" />
                  <p className="text-lg font-medium">Analyzing your project...</p>
                  <p className="text-sm">This may take a few seconds</p>
                </div>
              )}

              {response && !loading && (
                <div className="space-y-4">
                  <div className="prose prose-blue max-w-none">
                    <div 
                      className="text-gray-800 leading-relaxed response-content"
                      dangerouslySetInnerHTML={{ __html: formattedResponse }}
                    />
                  </div>

                  {metadata && (
                    <div className="mt-6 p-4 bg-gray-50 rounded-xl border border-gray-200">
                      <h4 className="font-medium text-gray-800 mb-2 flex items-center gap-2">
                        <Brain className="w-4 h-4" />
                        Analysis Details
                      </h4>
                      <div className="text-sm text-gray-600 space-y-1">
                        <p><strong>Model:</strong> {metadata.model}</p>
                        <p><strong>Tokens Used:</strong> {metadata.tokens_used}</p>
                        {metadata.was_truncated && (
                          <p className="text-yellow-600">
                            <strong>Note:</strong> Response was truncated due to length limits
                          </p>
                        )}
                        <p><strong>Generated:</strong> {new Date(metadata.timestamp).toLocaleString()}</p>
                        <p><strong>Response Length:</strong> {metadata.response_length} characters</p>
                      </div>
                    </div>
                  )}
                </div>
              )}

              {!response && !loading && !error && (
                <div className="flex flex-col items-center justify-center py-12 text-gray-400">
                  <Brain className="w-16 h-16 mb-4" />
                  <p className="text-lg">Your project analysis will appear here</p>
                  <p className="text-sm">Describe your project idea to get started</p>
                </div>
              )}
            </div>
          </div>
        </div>
      </div>

      {/* Add custom styles for better formatting */}
      <style jsx>{`
        .response-content h1,
        .response-content h2,
        .response-content h3 {
          margin-top: 1.5rem;
          margin-bottom: 0.75rem;
        }

        .response-content h1:first-child,
        .response-content h2:first-child,
        .response-content h3:first-child {
          margin-top: 0;
        }

        .response-content pre {
          white-space: pre-wrap;
          word-break: break-word;
        }

        .response-content code {
          word-break: break-word;
        }

        .response-content ul {
          list-style: none;
          padding-left: 0;
        }

        .response-content li::before {
          content: "•";
          color: #3b82f6;
          font-weight: bold;
          margin-right: 8px;
        }
      `}</style>
    </div>
  );
};

export default ProjectAnalyzer;